<template>
  <div class="liveroom">
    <video width="100%" id="videoElement" ref="videoElement"></video>
    <div class="msgmask">
      <div
        class="mask"
        @click="
          showtalk = false;
          showlist = false;
        "
      ></div>
      <div class="header">
        <img
          v-if="liveroommsg?.actorInfo"
          v-lazy="liveroommsg?.actorInfo?.avatar"
        />
        <div class="text">
          <p>{{ liveroommsg?.actorInfo?.uname }}</p>
          <span>热度 {{ liveroommsg?.onlineUserCount }}</span>
        </div>
        <div class="btn">关注</div>
      </div>
      <div class="footer">
        <div class="fr">
          <div
            class="bg"
            v-if="liveroommsg?.shopBagCount"
            @click="showlist = true"
          >
            <p>{{ liveroommsg?.shopBagCount }}</p>
          </div>
          <div class="input" @click.stop="showtalk = true">
            <p>说点什么...</p>
          </div>
          <div class="talk" v-if="showtalk">
            <input type="text" />
            <div class="btn">发送</div>
          </div>
        </div>
        <div class="fl">
          <div class="share">
            <div class="icon"></div>
          </div>
          <div class="good">
            <div class="icon"></div>
          </div>
        </div>

        <div class="list" v-if="showlist">
          <div class="mask"></div>
          <div class="title">全部商品 {{ queryDiscountPrice.length }}</div>
          <div class="content">
            <div
              class="item"
              v-for="item in queryDiscountPrice"
              :key="item.order"
            >
              <div class="fr" v-lazy:background-image="item.image">
                <p>{{ item.order }}</p>
              </div>
              <div class="fl">
                <dir class="tag">秒杀价</dir>
                <p>{{ item.title }}</p>
                <div class="msg">
                  <div class="price">
                    <del>&yen;{{ item.defaultPrice / 100 }}</del>
                    <p>&yen;{{ item.defaultPrice / 100 }}</p>
                  </div>
                  <div
                    class="btn"
                    @click="
                      goodsNo = item.order;
                      showgoods = true;
                    "
                  >
                    去购买
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="goods" v-if="showgoods">
          <div class="item">
            <div class="fr" v-lazy:background-image="goods[0]?.image"></div>
            <div class="fl">
              <dir class="title">
                <p>{{ goods[0]?.order }}号</p>
                <p class="tag">秒杀价</p>
                <van-icon name="cross" @click="showgoods = false" />
              </dir>
              <p class="text">{{ goods[0]?.title }}</p>
              <div class="msg">
                <div class="price">
                  <del>&yen;{{ goods[0]?.defaultPrice / 100 }}</del>
                  <p>&yen;{{ goods[0]?.defaultPrice / 100 }}</p>
                </div>
              </div>
            </div>
          </div>

          <div class="bar"></div>

          <div class="num">
            <div class="tag">数量</div>
            <div class="btns">
              <p
                @click="num == 1 ? (num = 1) : num--"
                :class="num == 1 ? 'active' : ''"
              >
                -
              </p>
              <p>{{ num }}</p>
              <p @click="num++">+</p>
            </div>
          </div>
          <div class="btn">
            <div
              class="fl"
              @click="
                getcartlist({
                  nums: num,
                  title: goods[0]?.title,
                  price: goods[0]?.defaultPrice / 100,
                  src: goods[0]?.image,
                  id: cartlist.length
                    ? Math.max(...cartlist.map((o) => o.id)) + 1
                    : 1,
                })
              "
            >
              加入购物车
            </div>
            <div class="fr">立即购买</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import flvjs from "flv.js";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      liveroommsg: null,
      showtalk: false,
      showlist: false,
      queryDiscountPrice: [],
      goodsNo: 0,
      showgoods: false,
      num: 1,
    };
  },

  created() {
    this.axios
      .get("https://apis.netstart.cn/mogujie/live/actorLiveInfo", {
        params: {
          roomId: this.$route.query.roomId,
          actorId: this.$route.query.actorId,
        },
      })
      .then((res) => {
        this.liveroommsg = res.data.data;
        this.$nextTick(() => {
          this.getflv();
        });
      });

    this.axios
      .get("https://apis.netstart.cn/mogujie/live/queryDiscountPrice", {
        params: {
          roomId: this.$route.query.roomId,
          actorId: this.$route.query.actorId,
        },
      })
      .then((res) => {
        this.queryDiscountPrice = res.data.data.itemList.reverse();
      });
  },

  computed: {
    goods: function () {
      return this.queryDiscountPrice.filter((o) => o.order === this.goodsNo);
    },
    ...mapState(["cartlist"]),
  },

  methods: {
    getflv: function () {
      if (flvjs.isSupported()) {
        let videoElement = this.$refs.videoElement;
        var flvPlayer = flvjs.createPlayer({
          type: "flv",
          url: this.liveroommsg?.liveUrl,
        });
        flvPlayer.attachMediaElement(videoElement);
        flvPlayer.load();
        flvPlayer.play();
      }
    },
    ...mapMutations(["getcartlist"]),
  },
  watch: {
    cartlist: function (goodsmsglist) {
      localStorage.setItem("cartlist", JSON.stringify(goodsmsglist));
    },
  },
};
</script>

